%title-font{
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
}

%line{
    content: '';
    position: absolute;
    background: #6D7278;
    height: 1px;
    bottom: 0;
    left: 0;
    right: 0;
}

$title: #363838;
$default: #5A5A5A;

@function max($numbers...) {
 @return m#{a}x(#{$numbers});
}
@function min($numbers...) {
 @return m#{i}n(#{$numbers});
}