@import 'vars';
@import 'fonts';
@import 'reset';
@import 'libs';
@import 'global';

.header{
    &__btn-menu{
        display: none;
    }

    &__inner{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 120px;
    } 
    &__btn{
        background-color: transparent;
        border: none;
        padding: 0;
        cursor: pointer;
    }
}

.logo{
    margin: 0 auto;
}

.menu{
    padding-left: 35px;
    padding-right: 60px;
    &__list{
        display: flex;
    }
    &__list-item + &__list-item{
        padding-left: 80px;
    }
    &__list-link{
        @extend %title-font;
        color: rgba(54, 56, 56, 0.9033);
        line-height: 22px;
        position: relative;
        &--active{
        padding-bottom: 4px;
        &::after{
            content: '';
            border-radius: 1px;
            position: absolute;
            height: 2px;
            left: 0;
            right: 0;
            bottom: 0;
            background: rgba($title, .7);
        }
        }
    }
}
.footer{
 &__content{
    background-color: #DFDFDF;
    padding: 60px 0 80px;
 }
 &__inner{
    display: flex;
    justify-content: space-between;
 }
 &__info{
     width: 540px;
 }
 &__info-text{
    margin-bottom: 15px;
    max-width: 476px;
 }
 &__form{
    display: flex;
 }
 &__form-input{
    border-radius: 21px;
    padding: 6px 10px 6px 18px;
    background-color: transparent;
    border: 1px solid rgba($title, .7);
    width: 100%;
    &::placeholder{
        font-family: 'Open Sans', sans serif;
        font-weight: 400;
        font-size: 16px;
        line-height: 27px;
        color: #A1A1A1;
    }
 }
 &__form-button{
    background-color: $title;
    border: none;
    color: #fffff0;
    font-weight: 600;
    border-radius: 20px;
    min-width: 160px;
    padding: 6px 12px;
    margin-left: 9px;
    cursor: pointer;
 }
 &__info-title{
     color: $title;
     @extend %title-font;
     margin-bottom: 14px;
     line-height: 23px;
 }
 &__social, &__menu{
    width: 125px;
 }
 &__social{
     margin-left: 100px;
     margin-right: auto;
 }
 &__social-link{
    padding-left: 28px;
    position: relative;
    &::before{
        content: '';
        top: 3px;
        left: 0;
        width: 16px;
        height: 16px;
        position: absolute;
    }
 }
 &__social-link--facebook::before{
    background-image:url(../img/facebook.svg);
 }
 &__social-link--instagram::before{
    background-image:url(../img/instagram.svg);
    width: 14px;
    height: 14px;
    top: 4px;   
}
&__social-link--pinterest::before{
    background-image: url(../img/Pinterest.svg);   
}
&__social-link--whatsapp::before{
    background-image: url(../img/whatsapp.svg);  
    width: 14px;
    height: 14px;  
}
&__social-link--youtube::before{
    background-image: url(../img/youtube.svg); 
    width: 18px;
    height: 12px; 
    top: 4px;  
}
 &__social-item, 
 &__menu-item{
    margin-bottom: 15px;
    font-weight: 600;
    font-size: 14px;
    color: #707070;

}
&__social-link, 
&__menu-link{
   color: #707070;
}
 &__menu{
     margin-right: 60px;
 }
 &__copyright{
    background-color: $title;
    text-align: center;
    color: #A1A1A1;
    padding: 42px 0;
}
}

.breadcrumbs{
    background: rgba(#DFDFDF, .5);
    padding: 14px;
    position: relative;
    z-index: 10;

    &__list{
        display: flex;
        overflow-x: auto;
    }
    &__item{
        position: relative;
        padding-right: 8px;
    }
    &__item::after{
        content: "/";
        position: absolute;
       
    }
    &__item:last-child::after{
        display: none;
    }
    &__link{
        font-size: 14px;
        color: $default;
    }
}

.rightside-menu{
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    max-width: 694px;
    background-color: #fff;
    padding-top: 40px;
    width: 100%;
    z-index: 20;
    box-shadow: -1px 2px 17px 0px rgb(82, 82, 82);
    height: 130%;
    transition: 2s;
    &.rightside-menu--close{
        transform: translateY(-200%);
        transition: all 2.5s ease;
        // display: none;
    }
    
    &__close{
        background-color: transparent;
        border: none;
        padding: 0;
        cursor: pointer;
        margin-left: 50px;
        margin-bottom: 60px;
    }
    &__content{
        text-align: center;
        max-width: 483px;
        margin: 0 auto;
    }
    &__logo{
        margin-bottom: 40px;
        display: inline-block;
    }
    &__title{
        color: $title;
        @extend %title-font;
        font-size: 36px;
        line-height: 51px;
        position: relative;
        margin-bottom: 17px;
        padding-bottom: 16px;
        &::after{
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            max-width: 252px;
            margin: 0 auto;
            height: 1px;
            background-color: #6D7278;
            border: 1px solid #979797;
        }
    }
    &__text{
        margin-bottom: 75px;
    }
    &__images{
        max-width: 483px;
    }
    }

    .top{
    &__inner{
        padding-bottom: 70px;
        position: relative;
        &::after{
            content: "";
            position: absolute;
            background-image: url(../img/main_slider.jpg);
            width: 100%;
            height: 722px;
            top: 0;
            z-index: -1;
        }
    }
    &__slider-item{
      display: flex !important;
      justify-content: center;  
      align-items: center;
    }
    &__slider-info{
     max-width: 645px;
     margin-left: 19px;
    }
    &__slider-title{
        color: $title;
        font-weight: 700;
        font-size: 110px;
        line-height: 134px;
        font-family: 'Montserrat';
        margin-bottom: 26px;
        padding-bottom: 26px;
        position: relative;
        &::after{
            content: '';
            position: absolute;
            left: 0;
            bottom: 0;
            width: 185px;
            background: #6D7278;
            height: 1px;
        }
    }
    &__slider-text{
        max-width: 585px;
    }
    &__slider-img{
        margin-left: -15%;
        max-width: 703px;
        height: 815px;
        object-fit: cover;
    }
    }

    //New collection start
    .new-collection{
        text-align: center;
        padding: 45px 0 35px;
        &__title{
            color: $title;
            font-size: 35px;
            line-height: 51px;
            padding-bottom: 25px;
            margin-bottom: 25px;
            position: relative;
            &::after{
                content: '';
                position: absolute;
                width: 250px;
                height: 1px;
                bottom: 0;
                right: 0;
                left: 0;
                margin: 0 auto;
                background-color: #6D7278
            }
        }
        &__text{
            max-width: 730px;
            margin: 0 auto 27px;
        }
    }

    .collection{
       display: grid;
       grid-template-columns: repeat(5, 1fr); 
       grid-gap: 30px;
       padding-bottom: 35px;
       &__item{
           position: relative;
           &:hover{
               .collection__info{
                   opacity: 1;
               }
           }
       }
       &__images{
           min-height: 350px;
           object-fit: cover;
       }
       &__info{
           position: absolute;
           background-color: rgba(#fff, .7);
           padding: 10px;
           display: flex;
           top: 0;
           left: 0;
           right: 0;
           bottom: 0;
           flex-direction: column;
           justify-content: center;
           align-items: center;
           opacity: 0;
           transition: all .8s;
           text-align: center;
       }
       &__info-title{
        @extend %title-font;
        font-size: 20px;
        line-height: 24px;
        color: #363838;
        position: relative;
        margin-bottom: 19px;
        padding-bottom: 19px;
        &::after{
            @extend %line;
            width: 80%;
            margin: 0 auto;
        }
       }
       &__info-text{
        font-weight: 600;
        font-size: 14px;
        line-height: 28px;
        color: #707070;
       }
    }

    //New collection end

    //decor start

    .decor{
        padding: 170px 0 155px;
        
        background-image: url(../img/content/decor.jpg);
        background-size: cover;
        background-repeat: no-repeat;
        &__title{
            color: $title;
            @extend %title-font;
            font-size: 60px;
            line-height: 80px;
            max-width: 500px;
            margin-bottom: 22px;
            padding-bottom: 22px;
            position: relative;
            &::after{
                position: absolute;
                @extend %line;
                max-width: 200px;
                
            }
        }
        &__text{
            line-height: 28px;
            max-width: 540px;
        }
    }

    //decor end

//work-path start

.works-path{
    padding: 80px 0 110px;
    &__title{
        text-align: center;
        margin-bottom: 40px;
        font-size: 36px;
        color: $title;
        @extend %title-font;
        line-height: 51px;
    }
    &__items-box{
        display: flex;
        border-bottom: 2px solid rgba($title, .7);
        position: relative;
        align-items: flex-end;
        &::after{
            content: "";
            position: absolute;
            bottom: -5px;
            left: 0;
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background-color: $title;
        }
    }
    &__item{
        max-width: 250px;
        display: flex;
        flex-wrap: wrap;
        position: relative;
        height: 100%;
        align-items: center;
    }
    &__item + &__item{
        margin-left: 317px;
    }
    &__item-title{
        margin-left: 10px;
        color: $title;
        @extend %title-font;
    }
    &__item-text{
        padding-top: 13px;
    }
    &__item--production{
        &::after{
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            height: 20px;
            width: 2px;
            background: rgba($title, .7);
        }
    }
    &__item--measuring{
        margin-left: 305px;
        padding-top: 38px;
        align-items: flex-start;
        &::before{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            height: 20px;
            width: 2px;
            background: rgba($title, .7);  
        }
    }
    &__item--consultation, &__item--production{
        padding-bottom: 38px;
    }
    
}

//work-path end

.gallery{
    margin-bottom: 80px;
    &__buttons{
        text-align: center;
        margin-bottom: 40px;
    }
    &__btn{
        border: 1px solid #454647;
        border-radius: 20px;
        padding: 6px 16px;
        opacity: 0.5;
        background-color: transparent;
        cursor: pointer;
        font-weight: 600;
        font-size: 14px;
        line-height: 28px;
        color: #8F8D8D;
    }
    
    &__btn + &__btn{
        margin-left: 25px;
    }
    &__items{
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: repeat(2, 1fr);
        grid-gap: 30px 35px;
    }
    &__item:first-child{
        grid-column: span 2;
        grid-row: span 2;
    }
    &__img{
        object-fit: cover;
        width: 100%;
        max-height: 1300px;
        height: 100%;
    }
    &-page{
        padding-top: 84px;
    }
    &.gallery-page{
        .gallery__item:nth-child(8){
            grid-column: span 2;
            grid-row: span 2;
        }
    }
}
.mixitup-control-active{
    background-color: rgba(#FFEF35, .5);
    color: $title;
    border: 1px solid rgba(#FFEF35, .5);
    opacity: 1;
}

.blog-box{
    padding-bottom: 100px;
    &__title{
        color: $title;
        text-align: center;
        @extend %title-font;
        font-size: 36px;
        line-height: 51px;
        margin: 0 auto 30px;
        max-width: 430px;
    }
    &__items{
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 30px;
    }
    &__img-link{
        display: inline-block;
        margin-bottom: 19px;
    }
    &__links{
        margin-bottom: 9px;
        font-size: 14px;
        font-weight: 600;
        color: #707070;
    }
    &__date, &__author, &__theme{
        color: inherit;
    }
    &__item-title{
        color: $title;
        font-size: 24px;
        line-height: 38px;
        @extend %title-font;
    }
}

.about{
    &__prev{
        background-repeat: no-repeat;
        background-position: top center;
        background-size: cover;
        text-align: center;
        padding: 160px 0;
        background-image: url(../img/about/about-bg.jpg);
        margin: -53px 0 130px;
    }
    &__prev-title{
        color: $title;
        font-size: 60px;
        line-height: 80px;
        @extend %title-font;
    }
    &__content{
        padding-bottom: 40px;
    }
    &__title{
        color: $title;
        @extend %title-font;
        font-size: 36px;
        line-height: 60px;
        max-width: 825px;
        margin-bottom: 25px;
        padding-bottom: 20px;
        position: relative;
        &::after{
            position: absolute;
            content: '';
            bottom: 0;
            left: 0;
            width: 200px;
            height: 1px;
            background: #6D7278;
            border: 1px solid #979797;
        }
    }
    &__text{
        p{
            padding-left: 284px;
            margin-bottom: 30px;
        }
    }
    &__popup{
        background-image: url(../img/about/popup-bg.jpg);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: top center;
        min-height: 414px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

.inspiration{
    padding-top: 100px;
    margin-bottom: 66px;
    position: relative;
    &::after{
        content: "";
        position: absolute;
        bottom: -52px;
        left: 0;
        right: 0;
        background-image: url(../img/inspiration-bg.jpg);
        height: 414px;
        z-index: -1;
    }
    &__inner{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__images{
        margin-right: 78px;
        margin-left: -230px;
    }
    &__title{
        color: $title;
        font-size: 36px;
        line-height: 60px;
        @extend %title-font;
        padding-bottom: 16px;
        margin-bottom: 24px;
        position: relative;
        &::after{
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            background: #6D7278;
            border: 1px solid #979797;
            border-radius: 1px;
            width: 200px;
            height: 2px;
        }
    }
    &__text{
        margin-bottom: 30px;
    }
    &__content{
        max-width: 825px;
    }
    &__item{
        @extend %title-font;
        color: $title;
        line-height: 23px;
        margin-bottom: 33px;
    }
    &__item-content{
        display: flex;
        justify-content: space-between;
        padding-bottom: 14px;
        border-bottom: 2px solid #fff;
        border-radius: 4px;
    }
    &__line{
        height: 2px;
        border-radius: 4px;
        background-color: $title;
        margin-top: -2px;
    }
}

.partners{
    &__items{
        display: grid;
        grid-template-columns: repeat(5, 1fr);    
        margin-bottom: 54px;
    }
    &__item{
        margin: 0 auto;
    }
}

.map{
    margin-top: -53px;
    iframe{
    height:578px;
    }
}

.contact{
    padding-bottom: 100px;
    padding-top: 55px;
    &__box{
        padding-left: 284px;
    }
    &__title{
        color: $title;
        @extend %title-font;
        padding-bottom: 20px;
        margin-bottom: 40px;
        position: relative;
        font-size: 36px;
        line-height: 60px;
        &::after{
            @extend %line;
            width: 200px;
            height: 2px;
        }
    }
    &__form-btn{
            background-color: $title;
            border: none;
            color: #fffff0;
            font-weight: 600;
            border-radius: 20px;
            min-width: 160px;
            padding: 6px 12px;
            cursor: pointer;
            display: inline-block;
            text-align: center;
            height: 40px;
         }
        &__social{
            display: flex;
            &-item + &-item{
                margin-left: 35px;
            }
        }
    &__text{
        margin: 20px 0 10px;
        color: #A1A1A1;
        line-height: 28px;
    }
    &__phone{
        color: $default;
    }
    &__city{
        padding-bottom: 16px;
    }
    &__form{
        padding-left: 284px;
        padding-top: 24px;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        align-items: center;
        
        &-input{
            border: 1px solid #DFDFDF;
            border-radius: 20px;
            padding: 6px 17px;
            margin-bottom: 13px;
            width: 49%;
        }
        &-area{
            border: 1px solid #DFDFDF;
            border-radius: 20px;
            width: 100%;
            padding: 14px 17px;
            margin-bottom: 20px;
            min-height: 60px;
        }
    }
    }

    .contact-slider{
        padding-bottom: 95px;
        &__link{
            position: relative;
            max-width: 170px;
            max-height: 170px;
            margin-bottom: 35px;
        }
        &__img{
            max-width: 170px;
            max-height: 170px;
            object-fit: cover;
        }
        &__title{
            &:hover{
                opacity: 1;
                transition: all .5s;
            }
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: rgba(#fff, .6);
            color: $title;
            @extend %title-font;
            line-height: 22px;
            text-align: center;
            padding: 0 10px 0 10px;
            opacity: 0;
        }
    }

    .blog{
        padding: 60px 0 100px;
        &__inner{
            display: flex;
            justify-content: space-between;
        }
        &__category{
            margin-bottom: 36px;      
        }
        &__category-title, &__tags-title{
            margin-bottom: 15px;
            line-height: 23;
            color: $title;
            @extend %title-font;
            line-height: 23px;
        }
        &__category-item{
            margin-bottom: 12px;
        }
        &__category-link{
            color: inherit;
        }
        &__tags{
            margin-bottom: 50px;
        }
        &__tags-link{
            font-weight: 600;
            font-size: 14px;
            line-height: 28px;
            text-align: center;
            color: #8F8D8D; 
            padding: 0 16px;
            margin-right: 10px;
            margin-bottom: 10px;            
            border: 1px solid rgba(#454647, .5);
            display: inline-block;
            border-radius: 14px;
        }
        &__social{
            display: flex;
        }
        &__social-link{
            margin-right: 20px;
        }
        &__items{
            max-width: 730px;
        }
        &__item{
            margin-bottom: 80px;
        }
        &__item-img{
            max-width: 730px;
        }
        &__item-info{
            padding: 25px 0 10px;
            font-size: 14px;
            font-weight: 600;
            color: #707070;
            a{
                color: inherit;
            }
        }
        &__item-title{
            line-height: 38px;
            margin-bottom: 6px;
            color: $title;
            @extend %title-font;
            font-size: 24px;
        }
        &__blockquote{
            background: rgba(223, 223, 223, 0.5);
            margin-bottom: 50px;
            padding: 54px 64px;
            font-style: italic;
            font-weight: 300;
            font-size: 24px;
            line-height: 38px;
            text-align: center;
        }
        &__blockquote-author{
            font-size: 16px;
            line-height: 22px;
            color: rgba(54, 56, 56, 0.9033);
            @extend %title-font;
            margin-top: 23px;
            padding-top: 23px;
            position: relative;
            font-style: normal;
            &::after{
                @extend %line;
                top: 0;
                max-width: 200px;
                background: #979797;
                opacity: 0.6;
                border-radius: 1px;
                margin: 0 auto;
                height: 2px;
            }
        }
        &__item-popup{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

    .article-popup{
        &__box{
            position: relative;
        }
    }

    .article-slider{
        &__arrow{
            padding: 0;
            border: none;
            background: transparent;
            top: 50%;
            position: absolute;
            transition: translateY(-50%);
            z-index: 10;
            cursor: pointer;
        }
        &__arrowleft{
            left: 16px;
        }
        &__arrowright{
            right: 16px;
        }
    }

    
    
    .recent-posts{
        &__title{
            margin-bottom: 24px;
            line-height: 23;
            color: $title;
            @extend %title-font;
            line-height: 23px; 
        }  
        &__item{
            margin-bottom: 11px;
        }
        &__item-title{
            display: block;
            color: inherit;
        }
        &__date, 
        &__author{
            color: #A1A1A1;
            font-size: 14px;
        }
        
    }
    

    .aside{
        max-width: 350px;
        margin-left: 30px;
        &__search{
            position: relative;
            margin-bottom: 40px;
        }
        &__search-input{
            padding: 6px 38px 6px 18px;
            border-radius: 20px;
            border: 1px solid rgba($title, .7);
            display: block;
            width: 100%;
            
            &::placeholder{
                color: #A1A1A1;
                font-size: 14px;
                font-family: 'Open Sans', sans-serif;
                line-height: 27px;
            }
        }
        &__search-btn{
            border: none;
            background-color: transparent;
            position: absolute;
            top: 11px;
            right: 9px;
            cursor: pointer;
        }
    }

    .pagination{
        display: flex;
        justify-content: center;
        align-items: center;
        &__list{
            display: flex;
        }
        &__list-item{
            margin: 0 6px;
        }
        &__arrow{
            margin: 0 6px;
        }
        &__arrow--disabled{
            display: none;
        }
        &__list-num{
            font-weight: 600;
            font-size: 14px;
            color: #707070;
        }
        &__list-num--active{
            color: #363838;
        }
    }

