@media(max-width: 1700px){
    .contact-slider{
        &__img{
            width: 100%;
        }
    }
}

@media(max-width: 1300px){
    .collection{
        grid-template-columns: repeat(3, 1fr);
        &__images{
            width: 100%;
        }
    }
     .map{
         margin-top: 0;
         iframe{
             height: 378px;
         }
     }
}

@media(max-width: 1201px){
    .top{
        &__slider-title{
            font-size: 62px;
            line-height: 74px;
        }
        &__slider-img{
            height: 640px;
        }
    }
    .works-path {
        padding: 80px 0 90px;
    }
    .contact-slider{
        &__img{
            max-height: 240px;
            max-width: 240px;
            height: 240px;
        }

    }
    .contact-slider__link {
        max-width: 240px;
        max-height: 240px;
    }
}

@media(max-width: 1040px){
    .blog{
        &__inner{
            flex-wrap: wrap;
        }
        &__items{
            margin-right: 0;
            max-width: 100%;
        }
        &__item-img{
            width: 100%;
            max-width: 100%;
        }
    }
        .aside{
            width: 100%;
            margin-left: 0;
            order: -1;
            margin-bottom: 50px;
        }
}

@media(max-width: 992px){
    .footer{
        &__info{
            width: 380px;
        }
    }
    .gallery{
        &__items{
            grid-gap: 15px;
        }
        &__btn + &__btn{
            margin: 1px;
        }
        &__btn{
            padding: 6px 10px;
        }
    }
    .works-path {
        padding: 80px 0 60px;
    }
    .about{
        &__text p{
            padding-left: 0;
        }
        &__contect{
            padding: 40px 0;
        }
        &__prev{
            margin-bottom: 90px;
        }
    }
    .inspiration__images{
        display: none;
    }
    .partners__items{
        grid-template-columns: repeat(4, 1fr);
    
    }
    .partner__item:last-child{
        display: none;
    }
}

@media(max-width: 840px){
    .inspiration {
        padding-top: 60px;
    }
    .top{
        &__inner{
            &::after{
                height: 100%;
            }
        }
        &__slider-title{
            font-size: 44px;
            line-height: 54px;
        }
        &__slider-img{
            height: 540px;
        }
    } 
    .collection{
        grid-template-columns: repeat(2, 1fr);
    }
    
    .works-path{
        &__item{
         max-width: 350px;
        }
    }
    .works-path__item + .works-path__item {
       margin-left: 50px;
    }
    .contact{
     .contact__box, .contact__form {
        padding-left: 0;
    }
    }
    .aside {
        margin-bottom: 30px;
    }
    .blog {
        padding: 50px 0 65px;
    }
    .blog__item{
        margin-bottom: 60px;
    }
}

@media(max-width: 768px){
    .header__btn{
        display: none;
    }
    .menu__list-item {
        padding-left: 0;
    }
    .menu + .menu{
        margin-left: 40px;
    }
    
    .logo{
        margin: 0;
        order: -1;
        margin-right: 10px;
    }
    .menu{
        padding-left: 0;
        padding-right: 0;
    }
    .menu__list-item + .menu__list-item{
        padding-right: 0;
    }

    .footer{
        &__inner{
            flex-wrap: wrap;
            justify-content: center;
        }
        &__info{
            width: 100%;
            margin-bottom: 50px;
        }
        &__info-text{
            max-width: 100%;
        }
        &__social{
            margin-left: auto;
            margin-right: auto;
        }
        &__menu{
            margin-left: auto;
            margin-right: auto; 
        }
    }
    .decor{
        padding: 70px 0 60px;
    }
    .works-path{
        padding: 70px 0 30px;
        &__item--measuring{
            margin-left: 120px;
    }
    }
    .gallery{
        margin-bottom: 60px;
        &__buttons{
            display: grid;
            justify-content: center;
            grid-template-columns: 1fr 1fr;
        }
        .gallery__btn + .gallery__btn, .gallery__btn {
            margin-bottom: 8px;
            margin-right: 10px;
        }
        &__items{
            grid-template-columns: repeat(2, 1fr);
        }
        &__item:first-child{
            grid-column: 2;
            grid-row: 1;
        }
    }
    .blog-box{
        padding-bottom: 60px;
    }
    .blog-box__item-title{
        font-size: 20px;
        line-height: 30px;
    }
    .about{
        &__prev{
            padding: 100px 0;
        }
        &__prev-title{
            font-size: 40px;
            line-height: 48px;
        }
    }
    .partners__items{
        grid-template-rows: repeat(2, 1fr);
        grid-template-columns: repeat(2, 1fr);
        margin-bottom: 30px;
    }
    .partner__item:nth-child(3){
        order: 1;
    }
    .gallery.gallery-page .gallery__item:nth-child(8) {
        grid-column: span 1;
        grid-row: span 1;
    }
}



@media(max-width: 650px){
    .inspiration {
        padding-top: 45px;
    }
    .top{
        &__slider-item{
            .top__slider-img{
                display: none;
            }
        }
        &__inner{
            padding-top: 30px;
            padding-bottom: 25px;
        }  
        &__slider-text{
            margin-bottom: 15px;
        }
    }
    .works-path{
        &__items-box::before,
        &__item--production::after,
        &__item--measuring::before,
        &__items-box::after{
            display: none;
        }
        &__items-box{
            border: none;
            flex-wrap: wrap;
            justify-content: center;
        }
        &__item--production{
            order: 1;
        }
        &__item--measuring{
            margin-left: 0;
            padding-top: 0;
            padding-bottom: 38px;
        }
        .works-path__item+.works-path__item{
            margin-left: 0;
        }
        &__item{
            max-width: 420px;
        }
    }
    
    }

    @media(max-width: 640px){
        .menu__list-item + .menu__list-item{
            padding-left: 60px;
        }
        .slick-dots li{
            margin: 0 5px;
        }
        .about__popup{
            min-height: 350px;
        }
            
    }


@media(max-width: 540px){
    .about__popup {
        min-height: 310px;
    }
    .contact-slider{
        padding-bottom: 20px;
        margin-top: -20px;
    }
    .logo{
        order: -1;
        margin-left: 15px;
        position: relative;
        z-index: 12;
    }
    .header{
        margin: 0 -15px;
        &__inner{
            flex-wrap: wrap;
            padding-top: 30px;
        }
        &__btn-menu{
            display: block;
            cursor: pointer;
            border: none;
            background-color: transparent;
            order: -1;
            margin-right: 15px;
            position: relative;
            z-index: 12;
        }
    }
    .menu{
        padding-right: 0;
        width: 100%;
        background-color: #fff;
        position: relative;
        z-index: 11;
        text-align: center;
        transform: translateY(-400px);
        transition: all 1s; 
        &--open{
            transform: translateY(30px); 
        }
        &__list{
            display: block;
        }
        &__list-link{
            display: block;
            padding: 15px 0;
        }
    }
    .menu__list-item + .menu__list-item {
        padding-left: 0;
    }
    .collection{
        grid-template-columns: repeat(1, 1fr);
    }
    .new-collection__title{
     font-size: 28px;
     line-height: 36px;
    }
    %title-font{
     font-size: 28px;
     line-height: 36px;
    }
    .works-path{
        &__item-title{
            font-size: 16px;
            line-height: 27px;
        }
    }
    .gallery{
        &__buttons{
            grid-template-columns: 1fr;
        }
    }
    .gallery{
        &__items{
        grid-template-columns: repeat(1, 1fr);
    }
    &__buttons{
        margin-bottom: 20px;
    }
    }
    .blog-box{
        padding-bottom: 40px;
        &__items{
            display: block;
        }
        &__img{
            width: 100%;
        }
        &__item{
            margin-bottom: 20px;
        }
    }
    .blog-box__item-title{
        font-size: 22px;
        line-height: 30px;
    }
    .inspiration{
        &__item-text, &__item-num{
            font-size: 16px;
            line-height: 23px;
            margin-right: 15px;
        }
        
}
.contact__form-input{
    width: 100%;
}

.menu__list-link--active::after{
    display: none;
}
.blog__item-title, 
.recent-posts__title, 
.blog__tags-title,
.blog__blockquote-author{
    font-size: 16px;
    line-height: 23px;
}
.blog__category-title{
    font-size: 28px;
    line-height: 35px;
}
}

@media(max-width: 390px){
    .about__popup {
        min-height: 270px;
    }
 .footer{
     &__inner{
         justify-content: space-between;
     }
     &__social{
        margin-left: 0;
     }
     &__form{
         display: block;
     }
     &__form-button{
         width: 100%;
         margin-left: 0;
         margin-top: 10px;
     }
 }
    .partner__item:nth-child(3), .partner__item:nth-child(4){
        display: none;
    }
    .partners__items{
        grid-template-rows: repeat(2, 1fr);
        grid-template-columns: repeat(1, 1fr);
    }
    .partner__item{
        max-width: 70%;
        margin-left: 15%;
        text-align: center;
    }
    .contact-slider{
        &__img{
            max-width: 360px;
            width: 360px;
            max-height: 360px;
            height: 360px;
        }
    }
    .contact-slider__link {
        max-width: 360px;
        max-height: 360px;
    }
}
