html,
body {
	height: 100%;
  width: 100%;
  overflow-x: hidden;
}

body{
	font-family: 'Open sans', sans-serif;
	font-size: 16px;
	line-height: 27px;
	font-weight: 400;
	color: $default;
	overflow-x: hidden;
	min-width: 320px;
}

.wrapper {
	display: flex;
	flex-direction: column;
	min-height: 100%;
}

.main {
	flex: 1 0 auto;
}
.footer {
	flex: 0 0 auto;
}

.container{
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}

.container-fluid{
	max-width: 1920px;
	margin: 0 auto;
	padding: 0 15px;
}

.slick-dots{
  display: flex;
  justify-content: center;
  align-items: center;  
  li{
	  margin: 0 15px;
  } 
  button{
	  background-color: $title;
	  border: none;
	  width: 8px;
	  height: 8px;
	  border-radius: 50%;
	  padding: 0;
	  font-size: 0;
	  cursor: pointer;
  }   
  li.slick-active{
	  button{	
		background-color: #FFEF35;
	  }
  }
}

.info-email{
    font-weight: 600;
    font-size: 14;
    color: $title;
    margin-bottom: 22px;
    padding-left: 30px;
    position: relative;
    display: inline-block;
    &::before{
        content:'';
        background-image: url(../img/mail.svg);
        height: 12px;
        width: 16px;
        left: 0;
        top: 8px;
        position: absolute;
    }
 }




